@import-normalize;

body {
  box-sizing: border-box;
  font-family: sans-serif;
  line-height: 1.5;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
ul,
ol {
  margin: 0;
  padding: 0;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
